import React from 'react';

function TodoItemControls({ id, value, done, deleteTodo, editTodo, important, changeImportant }) {
  return (
    <div className="TodoItem__controls">
      <button className="TodoItem__button TodoItem__button--edit" onClick={e => editTodo()}>
        <img className="TodoItem__icon" src="svg/edit.svg" alt="edit" />
      </button>
      <button className="TodoItem__button TodoItem__button--important" onClick={e => { changeImportant(id, value, done, !important) }} >
        <img className="TodoItem__icon" src="svg/important.svg" alt="important" />
      </button>
      <button className="TodoItem__button TodoItem__button--trash" onClick={e => deleteTodo(id)}>
        <img className="TodoItem__icon" src="svg/trash.svg" alt="delete" />
      </button>
    </div>
  );
}

export default TodoItemControls;
